<template>
  <div
    class="admin-global-search-header"
  >
    <div class="admin-global-search-header__input">
      <input
        v-model="searchText"
        type="text"
        :placeholder="tp('globalSearch')"
      >
    </div>
    <GlobalSearchIcon name="search" @click="onSearch" />
    <GlobalSearchIcon name="close-thin" @click="closeGlobalSearch" />
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed } from 'vue'
import { useGoBack } from '@/shared/plugins/vue-router'
import { useActions, useGetters, useMutations } from '@/shared/plugins/vuex'
import { ADMIN_ROUTES_NAMES } from '@/admin/common/routes'
import { MODULES_NAMES } from '@/shared/common/modules'
import GlobalSearchIcon from '@/admin/components/GlobalSearch/GlobalSearchIcon'

export default {
  name: 'AdminGlobalSearchHeader',
  components: { GlobalSearchIcon },
  setup () {
    const { tp } = useI18n('adminGlobalSearch')
    const goBack = useGoBack({
      name: ADMIN_ROUTES_NAMES.DASHBOARD
    })

    const {
      getIsSearch,
      getText
    } = useGetters(MODULES_NAMES.GLOBAL_SEARCH)
    const {
      SET_IS_SEARCH: setIsSearch,
      SET_TEXT: setText
    } = useMutations(MODULES_NAMES.GLOBAL_SEARCH)
    const {
      fetchSearchData
    } = useActions(MODULES_NAMES.GLOBAL_SEARCH)

    const isSearch = computed({
      get: () => getIsSearch.value,
      set: setIsSearch
    })
    const searchText = computed({
      get: () => getText.value,
      set: setText
    })

    const closeGlobalSearch = () => {
      isSearch.value = false
      goBack()
    }
    const onSearch = () => {
      isSearch.value = true
      fetchSearchData()
    }

    return {
      tp,
      isSearch,
      searchText,
      onSearch,
      closeGlobalSearch
    }
  }
}
</script>

