<template>
  <div class="admin-global-search-table">
    <div class="admin-global-search-table__filters">
      <CheckButton v-model="filters.pinned">
        <div class="pinned-btn">
          <Icon name="pin" />
          Pinned
          <div class="pinned-btn__count">
            {{ count }}
          </div>
        </div>
      </CheckButton>
      <CheckButton v-model="filters.results">
        <div class="d-flex align-items-center">
          <Icon name="search" />
          <Text ml="11px">Results</Text>
        </div>
      </CheckButton>
    </div>
    <Table
      :columns="columns"
      :data="data"
    >
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ row, item, column }">
        <CellFormatter
          :row="row"
          :item="item"
          :column="column"
        />
      </template>
    </Table>
    <MoreButtons
      @more="fetchMoreData"
      @all="fetchMoreData"
    />
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed, ref, watch } from 'vue'
import { useActions, useGetters, useMutations } from '@/shared/plugins/vuex'
import { TABLE_TYPES } from '@/admin/common/tableTypes'
import { MODULES_NAMES } from '@/shared/common/modules'
import { STATUS, COLORS } from '@/shared/common/status'
import Icon from '@/shared/components/Icon/Icon'
import Table from '@/admin/components/Table/Table'
import CheckButton from '@/admin/components/CheckButton'
import MoreButtons from '@/admin/components/Table/MoreButtons'
import CellFormatter from '@/admin/components/Table/CellFormatter/CellFormatter'
import Text from '@/shared/components/UIKit/Text/Text'

export default {
  name: 'AdminGlobalSearch',
  components: {
    Text,
    Icon,
    Table,
    CheckButton,
    MoreButtons,
    CellFormatter
  },
  setup () {
    const { t } = useI18n()
    const {
      getTableData: data,
      getTableColumns: columns,
      getFilters
    } = useGetters(MODULES_NAMES.GLOBAL_SEARCH)
    const {
      SET_FILTERS
    } = useMutations(MODULES_NAMES.GLOBAL_SEARCH)
    const {
      fetchSearchData,
      fetchMoreData
    } = useActions(MODULES_NAMES.GLOBAL_SEARCH)

    const count = ref(11)
    const filters = computed({
      get: () => getFilters.value,
      set: SET_FILTERS
    })

    watch(filters, () => {
      fetchSearchData()
    }, { deep: true, immediate: true })

    return {
      t,
      data,
      count,
      STATUS,
      COLORS,
      filters,
      columns,
      TABLE_TYPES,
      fetchMoreData
    }
  }
}
</script>

